.visually-hidden {
  position: absolute;
  width: 1px;
  height: 1px;
  margin: -1px;
  border: 0;
  padding: 0;
  white-space: nowrap;
  clip-path: inset(100%);
  clip: rect(0 0 0 0);
  overflow: hidden;
}

.page {
  font-family: "Oswald", sans-serif;
  font-size: 14px;
  line-height: 18px;
  font-weight: 400;
  background-color: #ffffff;
}
.page-main__title {
  font-size: 36px;
  line-height: 36px;
  font-weight: 400;
}
.page-main__description {
  text-transform: uppercase;
}
.page-main__section {
  padding: 0 20px;
}

.page-header {
  display: flex;
  min-height: 66px;
  border-bottom: 1px solid #e6e6e6;
}
@media (min-width: 768px) {
  .page-header {
    min-height: 76px;
    border: none;
  }
}
@media (min-width: 1440px) {
  .page-header {
    min-height: 107px;
  }
}

.page-header__wrapper {
  position: relative;
  display: flex;
  justify-content: space-between;
  flex-grow: 1;
  padding: 0 20px;
}
@media (min-width: 768px) {
  .page-header__wrapper {
    position: static;
    padding: 0 30px;
  }
}
@media (min-width: 1440px) {
  .page-header__wrapper {
    margin-top: auto;
    padding: 0 110px;
  }
}

.page-header__logo {
  display: flex;
  justify-content: space-between;
  align-items: center;
  min-width: 190px;
  padding: 12px 0 16px;
}
@media (min-width: 768px) {
  .page-header__logo {
    width: 174px;
    background: url("../../img/logo-tablet.svg") no-repeat center;
  }
  .page-header__logo img {
    display: none;
  }
}
@media (min-width: 1440px) {
  .page-header__logo {
    width: 202px;
    background: url("../../img/logo-desktop.svg") no-repeat center;
  }
}

.page-header__nav {
  display: flex;
  align-items: center;
  font-size: 20px;
  line-height: 20px;
  font-weight: 500;
  text-transform: uppercase;
}
@media (min-width: 768px) {
  .page-header__nav {
    font-size: 18px;
    line-height: 24px;
    font-weight: 400;
  }
  .page-header__nav button {
    display: none;
  }
}

.page-header__nav-toggle {
  margin-left: auto;
  border: none;
  width: 30px;
  height: 30px;
  background: url("../../img/toggler.svg") no-repeat center;
}

.page-header__list {
  position: absolute;
  top: 67px;
  left: 0;
  right: 0;
  padding: 0;
  margin: 0;
  list-style-type: none;
  background-color: #ffffff;
}
@media (min-width: 768px) {
  .page-header__list {
    position: static;
    display: flex;
    flex-wrap: wrap;
  }
}

.page-header__item {
  display: flex;
  justify-content: center;
  align-items: center;
  height: 66px;
  border-bottom: 1px solid #e6e6e6;
}
@media (min-width: 768px) {
  .page-header__item {
    border: none;
  }
}

.page-header__link {
  padding: 0;
  padding-right: 0;
  text-decoration: none;
  color: #000000;
}
@media (min-width: 768px) {
  .page-header__link {
    padding: 20px;
  }
}

.promo {
  padding: 0 20px 20px;
  color: #ffffff;
  text-align: center;
  background-image: url("../../img/index-color.png"), url("../../img/index-background.jpg");
  background-repeat: no-repeat;
  background-position: end 0;
}
.promo__title {
  margin: 25px 0;
  font-size: 36px;
  line-height: 36px;
  font-weight: 400;
}
.promo__slogan {
  margin: 0;
  margin-bottom: 29px;
}

.button {
  background-color: #68b738;
  border: none;
  text-transform: uppercase;
  text-align: center;
  text-decoration: none;
  color: #ffffff;
  cursor: pointer;
}

.programs__list {
  display: grid;
  grid-template-columns: 1fr;
  grid-row-gap: 20px;
  margin: 0;
}

.programs__container {
  display: grid;
  grid-template-columns: 50px 168px;
  grid-gap: 20px 22px;
  padding: 20px;
  background-color: #f2f2f2;
}

.programs__sign {
  grid-column: 1/2;
}

.programs__title {
  grid-column: 2/3;
  font-size: 24px;
  line-height: 37px;
}

.programs__description {
  grid-column: 1/3;
  margin: 0 0 15px 0;
}
.programs__description P {
  padding-bottom: 22px;
  border-bottom: 1px solid #d9d9d9;
}

.programs__button-catalog {
  display: block;
  color: #000000;
  text-decoration: none;
  font-size: 16px;
  line-height: 16px;
  text-transform: uppercase;
}

.programs__sign {
  display: block;
  width: 50px;
  height: 50px;
  background-color: #68b738;
  border-radius: 50%;
}

.description__list {
  margin: 0;
  padding: 0;
  list-style-type: none;
}

.illustration {
  background-color: #eaeaea;
}
.illustration__list {
  display: flex;
  flex-wrap: wrap;
}
.illustration__heading {
  font-size: 36px;
  font-weight: 400;
  line-height: 100%;
}
.illustration__description {
  text-align: center;
  font-size: 24px;
  line-height: 24px;
}

.invitation__title {
  font-style: normal;
  font-size: 16px;
  line-height: 20px;
  text-transform: uppercase;
  margin: 0;
}
.invitation__text {
  font-family: sans-serif;
  font-style: normal;
  line-height: 20px;
  color: #444444;
}
.invitation--inner {
  background-color: #f2f2f2;
}

.page-footer__list {
  display: flex;
  flex-wrap: wrap;
  padding: 0;
  list-style-type: none;
}
.page-footer__copyright {
  font-family: sans-serif;
  font-size: 16px;
  line-height: normal;
  color: #444444;
  text-decoration: none;
}

.card__link {
  text-decoration: none;
  color: #000000;
}
.card__title {
  font-size: 16px;
  line-height: 20px;
  font-weight: normal;
  text-transform: uppercase;
}

.more__title {
  font-size: 16px;
  line-height: 20px;
  font-weight: normal;
}
.more__text {
  font-size: 12px;
  line-height: 16px;
}
.more__button {
  color: #444444;
  background-color: #f2f2f2;
}

.additions-list__heading {
  font-size: 24px;
  line-height: 24px;
}
.additions-list__title {
  font-size: 16px;
  line-height: 16px;
  font-weight: normal;
  text-transform: uppercase;
}

.description-list {
  display: flex;
}

.present {
  display: flex;
  justify-content: center;
  align-items: center;
  height: 290px;
  background-image: url("../../img/index-color.png"), url("../../img/present.png");
  background-color: #68b738;
}
.present__text {
  font-family: sans-serif;
  font-size: 16px;
  line-height: 20px;
  color: #ffffff;
}

.field-group {
  border: none;
  margin: 0;
}
.field-group__title {
  font-size: 24px;
  line-height: 26px;
  text-transform: uppercase;
}
.field-group__textarea {
  border: 1px solid #e7e7e7;
  resize: none;
}
.field-group__list {
  margin: 0;
  padding: 0;
  list-style-type: none;
}
.field-group__item {
  margin-bottom: 14px;
}
.field-group:last-child {
  margin-bottom: 24px;
}

.field {
  display: flex;
  color: #444444;
}
.field__title {
  align-self: center;
  font-size: 16px;
  text-transform: uppercase;
}
.field__input {
  flex-grow: 1;
  font-size: 18px;
  text-transform: uppercase;
  padding: 14px;
  margin-left: 12px;
  border: 1px solid #e7e7e7;
  box-sizing: border-box;
  color: #444444;
}

.control {
  position: relative;
  display: flex;
  align-items: center;
}
.control__title {
  font-size: 16px;
  padding-left: 46px;
  text-transform: uppercase;
}
.control__control-marck {
  position: absolute;
  display: block;
  min-width: 30px;
  min-height: 30px;
  border: 1px solid #cdcdcd;
}
.control__control-marck--radio {
  border-radius: 50%;
}